import React from 'react';
import { Redirect, Route, HashRouter } from 'react-router-dom';
import { IonApp, IonPage, IonSplitPane } from '@ionic/react';
import { AppPage } from './declarations';

/* components */
import Menu from './components/Menu/Menu';
import Home from './pages/Home/Home';
import SignUp from './pages/SignUp/SignUp';

/* icons */
import { home, eye, people, planet, globe } from 'ionicons/icons';

/* Core CSS required for Ionic components to work properly */
import '@ionic/react/css/core.css';

/* Basic CSS for apps built with Ionic */
import '@ionic/react/css/normalize.css';
import '@ionic/react/css/structure.css';
import '@ionic/react/css/typography.css';

/* Optional CSS utils that can be commented out */
import '@ionic/react/css/padding.css';
import '@ionic/react/css/float-elements.css';
import '@ionic/react/css/text-alignment.css';
import '@ionic/react/css/text-transformation.css';
import '@ionic/react/css/flex-utils.css';
import '@ionic/react/css/display.css';

/* Theme variables */
import './theme/variables.css';

/* annimation */
import './theme/animate.css';
import Login from './pages/Login/Login';

const appPages: AppPage[] = [
  {
    title: 'Home',
    url: '#/home',
    icon: home
  },
  {
    title: 'Overview',
    url: '#/overview',
    icon: planet
  },
  {
    title: 'Services',
    url: '#/services',
    icon: globe
  },
  {
    title: 'Clients',
    url: '#/clients',
    icon: people
  },
  {
    title: 'Guards',
    url: '#/guards',
    icon: eye
  }
];

const App: React.FunctionComponent = () => (
  <IonApp>
    <HashRouter>
      <IonSplitPane contentId='main'>
        <Menu appPages={appPages} />
        <IonPage id='main'>
          <Route path='/home' component={Home} exact={true} />
          <Route path='/signup' component={SignUp} exact={true} />
          <Route path='/login' component={Login} exact={true} />
          <Route path='/overview' component={Home} exact={true} />
          <Route path='/clients' component={Home} exact={true} />
          <Route path='/guards' component={Home} exact={true} />
          <Route exact path='/' render={() => <Redirect to='/home' />} />
        </IonPage>
      </IonSplitPane>
    </HashRouter>
  </IonApp>
);

export default App;
